*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: "Quicksand", sans-serif;
    height: 100vh;
    background: url(../images/divdiv.png);
    background-position: center;
    background-size: cover;
    overflow-x: hidden;
}
header{
    margin-top: 30px;
}
a{
    text-decoration: none;
}

.header-navbar ul{
    display: flex;
    align-items: center;
    justify-content: center;
}
ul li{
    list-style-type: none;
}
.header-navbar ul li a{
    text-decoration: none;
    margin-left: 30px;
}
.header-navbar ul li:first-child{
    margin-right: auto;
}
.header-navbar ul li a{
    font-size: 20px;
    font-weight: 500;
    color: #5479f7;
}

.header-navbar-mobile .menuToggleBtn{
    color: #5479f7;
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: end;
}
.header-navbar-mobile{
    display: none;
}


.main{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.main h3{
    padding-left: 10px;
    margin: 30px 0px 10px;
}
.main p{
    text-align: left;
}
.main .row .col-md-6:first-child{
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
}

.features{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
}
.feature{
    cursor: pointer;
    height: 100px;
    width: 100px;
    background-color: #f4f5ff;
    border-radius: 50%;
    margin: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
}
.aboutText p{
    text-align: left;
}
.aboutText .row .col-12{
    display: flex;
    align-items: center;
    justify-content: center;
}
.profilePhoto{
    width: 300px;
    height: 300px;
    clip-path: circle(50%);
}
.social{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.social .col-12{
    display: flex;
    align-items: center;
    justify-content: center;
}
.socialBtn{
    height: 100px;
    width: 100px;
    margin: 70px;
    font-size: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
#linkedin:hover{
    color: #1b66c2 ;
}
#github{
    color: #1f2329;
}
#github:hover{
    color: #592c57;
}

.projects .row .col-md-4{
    display: flex;
    align-items: center;
    justify-content: center;
}
.projects .card{
    margin: 10px;
    border: none;
    box-shadow: 5px 5px 15px 5px rgba(30, 55, 421, 0.2);
}
.projects .card .card-body .reviewBtn{
    position: relative;
    padding: 5px 20px;
    height: 20px;
    width: 20px;
    border: 1px solid #000;
    border-radius: 5px;
}
.projects .card .card-body .reviewBtn::before{
    content: "";
    position: absolute;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    background-color: #5479f7;
    transition: .5s;
}
.projects .card .card-body .reviewBtn:hover::before{
    width: 100%;
}
.projects .card .card-body .reviewBtn span{
    color: #5479f7;
    position: relative;
    z-index: 100;
}
.projects .card .card-body .reviewBtn:hover span{
    color: #fff !important;
}
@media screen and (max-width:768px){
    .header-navbar-mobile{
        display: block;
    }
    .mobile-navbar{
        padding: 0px;
        background-color: #5479f7;
        opacity: 1;
        backdrop-filter: blur(5px);
        transition: 1s;
        overflow: hidden;
    }
    .mobile-navbar{
        display: flex;
        align-items: start;
        justify-content: center;
        flex-direction: column;
    }
    .mobile-navbar li {
        color: #fff !important;
        padding: 10px 0;
    }
    .mobile-navbar li a{
        color: #fff;
    }
    .header-navbar{
        display: none;
    }
    .socialBtn{
        margin: 0;
        font-size: 30px;
    }
}